.article {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  background: var(--color-footer);
  &__image {
    width: 100%;
    height: 100%;
    background: var(--color-bg);
  }
  &__image img {
    width: 100%;
    height: 100%;
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 16px 24px;
    height: 100%;
  }
  &__content p {
    font-family: var(--font-family);
    font-size: 11.65px;
    font-weight: 700;
    line-height: 35px;

    color: #fff;
  }
  &__content p:last-child {
    cursor: pointer;
  }
  &__content h3 {
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.3px;

    color: #fff;
    margin-bottom: 80px;
    cursor: pointer;
    @media screen and (max-width: 550px) {
      font-size: 18px;
      line-height: 25p;
    }
  }
}
